"use client";
import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import Link from "next/link";

export default function DashboardLayout({ children }) {
  const { data: session, status } = useSession();
  const router = useRouter();

  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/login");
    }
  }, [status, router]);

  if (status === "loading") return <p className="p-10 text-center">Loading...</p>;
  if (!session) return null;

  return (
    <div className="flex">
      <aside className="w-64 h-screen bg-gray-200 p-5">
        <h2 className="text-xl font-bold mb-5">Dashboard</h2>
        <ul className="space-y-3">
          <li>
            <Link href="/dashboard/add-product">âž• Add Product</Link>
          </li>
        </ul>
      </aside>
      <main className="flex-1 p-6">{children}</main>
    </div>
  );
}
